import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import LoadingOverlay from "react-loading-overlay";
import { useHistory } from "react-router-dom";
import {
  Grid,
  Card,
  MuiThemeProvider,
  TablePagination,
} from "@material-ui/core";
import routes from "../../routes/routes";
import { useDispatch, useSelector } from "react-redux";


const VendorList = (props) => {
  const history = useHistory();
  const [page, setPage] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [vendor, setVendor] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);

  const config = {
    headers: {
      Authorization: "Bearer " + userToken,
    },
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleProduct = (rowData) => {
    history.push("/dashbord/vendor/updateVendor", rowData);
  };

  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "dataIndex",
      label: "Number",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue, rowIndex) => (
          <span
            onClick={() => {
              handleProduct(vendor[tableMeta.rowIndex]);
            }}
            className="linkSpan"
          >
            {value}{" "}
          </span>
        ),
      },
    },
    {
      name: "description",
      label: "Description",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "vengorImage",
      label: "Image",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
        display: false,
      },
    },
    {
      name: "contactName",
      label: "Contact Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "contactEmail",
      label: "Contact Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "orderEmail",
      label: "Order Email",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "orderFax",
      label: "Order Fax",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "phone",
      label: "Phone",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let productData = [];

  const getAllVendors = async () => {
    setIsActive(true);
    setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.vendor.getAllVendors.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [product] = await axios.all([pData]);
    setTotalCount(product.data.totalDoc);
    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        name: data.name,
        description: data.description,
        vengorImage: data.vengorImage,
        contactName: data.contactName,
        contactEmail: data.contactEmail,
        orderEmail: data.orderEmail,
        orderFax: data.orderFax,
        phone: data.phone,
      };
      productData = [...productData, newdata];
      return productData;
    });
    setVendor(productData);
    setIsActive(false);
  };
  
  useEffect(() => {
    getAllVendors();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div>
        <div>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={vendor}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </div>
      </div>
    </LoadingOverlay>
  );
};
export default VendorList;
