import produce from "immer";
const initialstate = {
  userToken: "",
  user: {},
  isAuthenticated: false,

};

const LoginReducer = produce((state = {}, action) => {
  switch (action.type) {
    case "SET_AUTH_INFO": {
      state.isAuthenticated = true;
      state.userToken = action.payload.token
      state.user = action.payload;
      // state.userToken = action.payload.token;
      return state;
    }
    case "LOGOUT": {
      return initialstate;
    }

    default:
      return state;
  }
});

export default LoginReducer;
