import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import {
  Box,
  Card,
  Grid,
  Modal,
  Button,
  useTheme,
  Container,
  TextField,
  DialogTitle,
  DialogContent,
  TablePagination,
  MuiThemeProvider,
} from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";
import LoadingOverlay from "react-loading-overlay";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";
import QRCodeDialogBox from "./QRCodeDialogBox";
import routes from '../../routes/routes';
import { useDispatch, useSelector } from "react-redux";

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};


export default function PartsCabinetList() {
  const [page, setPage] = useState(0);
  const [rooms, setRooms] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [partsCabinet, setPartsCabinet] = useState([]);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [opartsCabinet, setOPartsCabinet] = useState([]);
  const [modalOpen, setModalOpen] = React.useState(false);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)
  
  const theme = useTheme();

  const handleModalOpen = () => setModalOpen(true);

  const handleModalClose = () => setModalOpen(false);
  

  const EditClick = (event, _id, tableMeta) => {
    const data = partsCabinet.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = opartsCabinet.find((item) => item._id === data._id);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleCloseModal = () => {
    setIsQRModal(false);
    setIsOpenModal(false);
    setRefresh(!refresh);
  };

  const handleModal = () => {
    setIsOpenModal(true);
  };

  const columns = [
    {
      name: "no",
      label: "Cabinet No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "name",
      label: "Parts Cabinet Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let partsCabinetData = [];

  const getAllPartsCabinets = async () => {
    setIsActive(true)

    const cData = axios({
      method: "get",
      url: routes.api.vehicle.getAllCabinets.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const rData = axios({
      method: "get",
      url: routes.api.vehicle.getAllRooms.url,
      headers: {Authorization: "Bearer " + userToken},
    });

    const [cabinets, rooms] = await axios.all([cData, rData]);

    if (rooms.data.success) {
      setRooms(rooms.data.cabinets);
    }

    if (cabinets.data.success) {
      setTotalCount(cabinets.data.cabinets.length);

      cabinets.data.cabinets.map((data) => {
        const newdata = {
          _id: data._id,
          no: data.no,
          name: data.name,
          r_name: data.room_id,
        };
        partsCabinetData = [...partsCabinetData, newdata];
        return partsCabinetData;
      });
      setPartsCabinet(partsCabinetData);
      setOPartsCabinet(cabinets.data.cabinets);
    }
    setIsActive(false)
  };

  useEffect(() => {
    getAllPartsCabinets();
  }, [refresh, page, rowsPerPage]);

  const notify = () => {
    toast.success("Added Successfully!", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const partsCabinetSchema = Yup.object().shape({
    room_id: Yup.string().required("Required"),
    name: Yup.string("String").required("Required"),
  });

  const formik = useFormik(
    {
    enableReinitialize: true,
    initialValues: {
      name: "",
      room_id: "",
    },

    validationSchema: partsCabinetSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      axios({
        method: "post",
        url: routes.api.vehicle.addNewPartsCabinet.url,
        data: values,
        headers: {Authorization: "Bearer " + userToken},
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            notify()
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });
  
  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (   
<LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
      <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              onClick={handleModal}
              className="primaryButton"
            >
              New Parts Cabinet
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={partsCabinet}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>

          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="cabinet"
            editItem={editItem}
            onClose={handleCloseModal}
            />
            </Box>
          </Modal>
        ) : null}
        {isOpenModal ? (
          <Dialog open={isOpenModal} onClose={handleCloseModal}>
            <DialogTitle>Add Parts Cabinet</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      <TextField
                        select
                        fullWidth
                        label="Room"
                        {...getFieldProps("room_id")}
                        SelectProps={{ native: true }}
                      >
                        <option value="" />
                        {rooms.map((option) => (
                          <option key={`${option._id}`} value={option._id}>
                            {option.name}
                          </option>
                        ))}
                      </TextField>
                    </Grid>

                    <Grid item xs={12} sm={12}>
                      <TextField
                        // select
                        fullWidth
                        label="Parts Cabinet Name"
                        // inputProps={{ type: 'number' }}
                        {...getFieldProps("name")}
                        error={Boolean(touched.name && errors.name)}
                        helperText={touched.name && errors.name}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : null}
      </Container>
    </div>
    </LoadingOverlay>
  );
}
