import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import { Waypoint } from "react-waypoint";
import LoadingOverlay from "react-loading-overlay";
import Button from "@material-ui/core/Button";
import {
  Grid,
  Card,
  MuiThemeProvider,
  TablePagination,
  Link,
  Chip,
  TextField,
} from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";
import CustomTextField from "../../components/CustomTextField";
import InputLabel from "@material-ui/core/InputLabel";
import { useSelector } from "react-redux";
import routes from "../../routes/routes";
import { useHistory, useLocation } from "react-router-dom";

const EditVendors = ({ handleModalClose, handleCloseMenuButton2 }) => {
  const location = useLocation();
  // console.log("location", location.state.data)

  const [page, setPage] = useState(0);
  const [page2, setPage2] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [warehouses, setWarehouses] = useState([]);
  const [totalCount2, setTotalCount2] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(100);
  const [rowsPerPage2, setRowsPerPage2] = useState(100);
  const [selectedForDelete, setSelectedForDelete] = useState([]);
  const [parentCode, setParentCode] = useState(location.state.data);
  const [allVendor, setAllVendor] = useState([]);
  const [vendor, setVendor] = useState([]);
  const [assignVendor, setAssignVendor] = useState([]);
  const [inputList, setInputList] = useState([
    {
      name: "",
      sku: "",
      barcode: "",
      price: "",
      cost: "",
      parl: "",
      quantity: ``,
    },
  ]);

  const history = useHistory();

  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);

  const top100Films = [
    { title: "The Shawshank Redemption", year: 1994 },
    { title: "The Godfather", year: 1972 },
    { title: "The Godfather: Part II", year: 1974 },
    { title: "The Dark Knight", year: 2008 },
    { title: "12 Angry Men", year: 1957 },
    { title: "Schindler's List", year: 1993 },
    { title: "Pulp Fiction", year: 1994 },
  ];

  let variantData = [];

  const deleteProduct = () => {
    const ids = selectedForDelete.map((item) => item._id);

    if (ids.length > 0) {
      axios({
        method: "post",
        url: routes.api.product.deleteProduct.url,
        data: { ids },
        headers: { Authorization: "Bearer " + userToken },
      }).then((res) => {
        if (res.data.success === true) {
          handleCloseMenuButton2();
        }
      });
    }
  };

  let dummyarr = [];

  const handleActionAdd = (rowData) => {
    dummyarr = selectedForDelete;
    dummyarr.push(rowData);
    setSelectedForDelete([...dummyarr]);
    setTotalCount2(selectedForDelete.length);
  };

  const handleDisable = (value) => {
    const res = selectedForDelete.find((item) => item._id === value);
    return res ? true : false;
  };

  const handleActionDelete = (value) => {
    let deletedArray = selectedForDelete.filter((x) => x._id !== value);
    setSelectedForDelete(deletedArray);
  };

  const handleClose = () => {
    handleModalClose();
  };

  const addAllVariants = () => {
    // alert("helllo");
    setSelectedForDelete(warehouses);
  };

  const removeAllVariants = () => {
    // alert("helllo");
    setSelectedForDelete([]);
  };

  // all columns
  const columns = [

    // All Variants
    {
      name: "name",
      label: "Variant Names",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          // console.log(tableMeta.rowData, "rowtableMeta")
          return (
            <>
              {tableMeta.rowIndex === warehouses.length - 5 ? (
                <Waypoint
                  onEnter={() => {
                    setPage(page + 1);
                    // setRowsPerPage(rowsPerPage+500)
                  }}
                />
              ) : null}
              <div>
                {tableMeta.rowData[0]}
                {/* <br />
                {tableMeta.rowData[2]}
                <br />
                SKU {tableMeta.rowData[3]}
                <br />
                Qty in stock: {tableMeta.rowData[4]} */}
              </div>
            </>
          );
        },
      },
    },
    // action
    {
      name: "_id",
      label: "Vendors",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div className="pt-3 inputBlock">
              <Autocomplete
                multiple
                id="tags-filled"
                onChange={(event, value) => handleChangeAutoComplete(tableMeta.rowIndex,tableMeta, value , event)}
                options={vendor.map((option) => JSON.stringify(option))}
                // defaultValue={[top100Films[1].title]}
                renderOption={(props, option) => JSON.parse(props).name}
                freeSolo
                renderTags={(value, getTagProps) =>
                  value.map((option, index) => (
                    <Chip
                      variant="outlined"
                      label={JSON.parse(option).name}
                      {...getTagProps({ index })}
                    />
                  ))
                }
                renderInput={(params) => (
                  <TextField
                    {...params}
                    variant="filled"
                    // label="freeSolo"
                    placeholder="Select Vendors..."
                  />
                )}
              />
            </div>
          );
        },
      },
    },
  ];

  // all options
  const option = {
    filterType: "none",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    print: false,
    download: false,
    search: false,
    filter: false,
    viewColumns: false,
    responsive: "vertical",
    tableBodyHeight: "400px",
    tableBodyMaxHeight: "100%",
    rowsPerPage: [100],
  };
let arrayofvandor =[]
let newarr =[]
let newarr2 =[]
let FilterData=[]

  const handleChangeAutoComplete = (rowIndex,tableMeta, value, event) => {
    event.preventDefault()
    let newarr=[];
    let rowData = inputList[rowIndex]._id
    let vendorIds = value.map(data=>JSON.parse(data)._id)
    // arrayofvandor.push({
    //    _id: rowData,
    //   vendorId: vendorIds}
    //   )
    // const ids = arrayofvandor.map((o) => o._id);
    // const filtered = arrayofvandor.filter(
    //   ({ _id }, index) => !ids.includes(_id, index + 1)
    // );
    // FilterData =[...filtered]
    // newarr.push(filtered)
    // console.log("FilterData", newarr)
    setAssignVendor([...assignVendor,{_id:rowData,vendor_id:vendorIds}])
    // FilterData =[...assignVendor]

  }

  const handleSubmitVendor =(event)=>{
    event.preventDefault()
    
    const ids = assignVendor.map((o) => o._id);
    const filtered = assignVendor.filter(
    ({ _id }, index) => !ids.includes(_id, index + 1)
    );
    console.log("filtered", filtered)

    axios({
      method: "post",
      url: routes.api.product.AssignVendorToProduct.url,
      data: filtered,
      headers: { Authorization: "Bearer " + userToken },
    }).then((res) => {
      if (res.data.success === true) {
  console.log("success")
      }
    });



  }



  let productData = [];

  const getAllVendors = async () => {
    let vendorData = [];

    // setIsActive(true);
    // setRefresh(true);
    const pData = axios({
      method: "get",
      url: routes.api.vendor.getAllVendors.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [product] = await axios.all([pData]);
    setTotalCount(product.data.totalDoc);
    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        name: data.name,
        description: data.description,
        venorImage: data.vengorImage,
        contactName: data.contactName,
        contactEmail: data.contactEmail,
        orderEmail: data.orderEmail,
        orderFax: data.orderFax,
        phone: data.phone,
      };

      vendorData = [...vendorData, newdata];
      return vendorData;
    });
    console.log(vendorData, "vendorDatafromvendorspage");
    setVendor(vendorData);
    // setIsActive(false);
  };
  // console.log("vendor",vendor)

  const getAllProducts = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
      headers: { Authorization: "Bearer " + userToken },
    });
    const [product] = await axios.all([pData]);

    if (product.data.success) {
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          dataIndex: index + 1,
          id: data.id,
          name: data.name,
          category: data.categories,
          sku: data.sku,
          barcode: data.barcode,
          cost: data.cost,
          quantity: data.quantity,
        };
        productData = [...productData, newdata];
        return productData;
      });
      setWarehouses([...warehouses, ...productData]);
    }
    setIsActive(false);
  };

  const getAllVariant = async () => {
    let VendorsData = [];
    const pData = axios({
      method: "get",
      url: routes.api.product.getVariantsByProductID.url,
      params: { parantCode: parentCode },
      headers: { Authorization: "Bearer " + userToken },
    });
    // console.log("pdata", pData);
    const [variants] = await axios.all([pData]);
    // console.log("variants", variants);

    if (variants.data.success) {
      variants.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          barcode: data.barcode,
          name: data.name,
          parl: data.parl,
          price: data.price,
          sku: data.sku,
          barcode: data.barcode,
          cost: data.cost,
          quantity: data.quantity,
          status: data.status,
        };
        // VendorsData = [...VendorsData, ...data.vendor_id];

        variantData = [...variantData, newdata];
        // console.log(VendorsData, "VendorsData");
        return variantData;
      });
      const ids = VendorsData.map((o) => o._id);
      const filtered = VendorsData.filter(
        ({ _id }, index) => !ids.includes(_id, index + 1)
      );
      // console.log();
      setAllVendor(filtered);
      console.log(allVendor, "allvendorsassigned to variant");
      setInputList(variantData);
    }
  };

  const onPageLoad = () => {
    getAllVendors();
    getAllVariant();
  };

  useEffect(() => {
    onPageLoad();
  }, [refresh, page, rowsPerPage]);

  const options = vendor.map((option) => ({
    id: option._id,
    label: option.name,
  }));

  return (
    // Loading Overlay
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div
        style={{
          height: "85vh",
          width: "90%",
          marginLeft: "50px",
          marginTop: "50px",
        }}
      >
        {/* Heading */}
        <div className="Texts_Display"> Edit vendors</div>

        {/* Tables */}
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-evenly",
            alignItems: "center",
            // height: "60vh"
          }}
        >
          {/* 1st table */}
          <div style={{ width: "100%" }}>
            <Grid style={{ width: "100%" }} item xs={12} sm={12}>
              <Card style={{ width: "100%" }}>
                <MuiThemeProvider>
                  <MUIDataTable
                    style={{ th: { backgroundColor: "grey" } }}
                    title={
                      <div
                        style={{
                          fontSize: "1.25rem",
                          fontFamily: "Arial",
                          fontWeight: "500",
                          lineHeight: "1.6",
                          letterSpacing: "0.0075em",
                        }}
                      >
                        Shopventory variants can have multiple vendors. Learn
                        more
                      </div>
                    }
                    data={inputList}
                    columns={columns}
                    options={option}
                  />
                </MuiThemeProvider>
              </Card>
            </Grid>
          </div>
        </div>

        {/* Buttons */}
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "flex-end",
            alignItems: "center",
            // padding: "20px",
          }}
        >
          <div>
            <Button
              variant="contained"
              onClick={() => {
                history.goBack();
              }}
            >
              Cancel
            </Button>
          </div>
          <div>
            <Button
              // onClick={deleteProduct}
              className="buttonColorAdd"
              variant="contained"
              onClick={(event)=>handleSubmitVendor(event)}
            >
              Update
            </Button>
          </div>
        </div>
      </div>
    </LoadingOverlay>
  );
};

export default EditVendors;
