import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import { Waypoint } from "react-waypoint";
import LoadingOverlay from "react-loading-overlay";
import Button from "@material-ui/core/Button";
import {
  Grid,
  Card,
  MuiThemeProvider,
  TablePagination,
  Link,
} from "@material-ui/core";
import { useSelector } from "react-redux";
import routes from "../../routes/routes";


const DeleteProducts = ({ handleModalClose, handleCloseMenuButton2 }) => {
  const [page, setPage] = useState(0);
  const [page2, setPage2] = useState(0);
  const [refresh, setRefresh] = useState(false);
  const [isActive, setIsActive] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [warehouses, setWarehouses] = useState([]);
  const [totalCount2, setTotalCount2] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(100);
  const [rowsPerPage2, setRowsPerPage2] = useState(100);
  const [selectedForDelete, setSelectedForDelete] = useState([]);

  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);

  const deleteProduct = () => {
    const ids = selectedForDelete.map((item) => item._id);

    if (ids.length > 0) {
      axios({
        method: "post",
        url: routes.api.product.deleteProduct.url,
        data: { ids },
        headers: {Authorization: "Bearer " + userToken},
      }).then((res) => {
        if (res.data.success === true) {
          handleCloseMenuButton2();
        }
      });
    }
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangePage2 = (event, newPage) => {
    setPage2(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleChangeRowsPerPage2 = (event) => {
    setRowsPerPage2(parseInt(event.target.value, 10));
  };

  let dummyarr = [];

  const handleActionAdd = (rowData) => {
    dummyarr = selectedForDelete;
    dummyarr.push(rowData);
    setSelectedForDelete([...dummyarr]);
    setTotalCount2(selectedForDelete.length);
  };

  const handleDisable = (value) => {
    const res = selectedForDelete.find((item) => item._id === value);
    return res ? true : false;
  };

  const handleActionDelete = (value) => {
    let deletedArray = selectedForDelete.filter((x) => x._id !== value);
    setSelectedForDelete(deletedArray);
  };

  const handleClose = () => {
    handleModalClose();
  };

  const addAllVariants = () => {
    // alert("helllo");
    setSelectedForDelete(warehouses);
  };

  const removeAllVariants = () => {
    // alert("helllo");
    setSelectedForDelete([]);
  };

  // all columns
  const columns = [
    // id
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    // name
    {
      name: "name",
      label: "Name",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // productCategory
    {
      name: "category",
      label: "Category",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // sku
    {
      name: "sku",
      label: "SKU",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // quantity
    {
      name: "quantity",
      label: "Quantity",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // All Variants
    {
      name: "name",
      label: "All Variants",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <>
              {tableMeta.rowIndex === warehouses.length - 5 ? (
                <Waypoint
                  onEnter={() => {
                    setPage(page + 1);
                    // setRowsPerPage(rowsPerPage+500)
                  }}
                />
              ) : null}
              <div>
                {tableMeta.rowData[1]}
                <br />
                {tableMeta.rowData[2]}
                <br />
                SKU {tableMeta.rowData[3]}
                <br />
                Qty in stock: {tableMeta.rowData[4]}
              </div>
            </>
          );
        },
      },
    },
    // action
    {
      name: "_id",
      label: "Action",
      options: {
        // display: tru,
        // filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <Button
                variant="contained"
                className="buttonColorAdd"
                disabled={handleDisable(value)}
                onClick={() => handleActionAdd(warehouses[tableMeta.rowIndex])}
              >
                +
              </Button>
            </div>
          );
        },
      },
    },
  ];

  // to be deleted colums
  const columns2 = [
    // id
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    // productName
    {
      name: "name",
      label: "Name",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // productCategory
    {
      name: "category",
      label: "Category",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // sku
    {
      name: "sku",
      label: "SKU",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // quantity
    {
      name: "quantity",
      label: "Quantity",
      options: {
        display: false,
        // filter: true,
        // sort: true,
        // customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // Variants to Delete
    {
      name: "name",
      label: "Variants to Delete",
      options: {
        filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              {tableMeta.rowData[1]}
              <br />
              {tableMeta.rowData[2]}
              <br />
              SKU {tableMeta.rowData[3]}
              <br />
              Qty in stock: {tableMeta.rowData[4]}
            </div>
          );
        },
      },
    },
    // action
    {
      name: "_id",
      label: "Action",
      options: {
        // display: tru,
        // filter: true,
        // sort: true,
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div>
              <Button
                variant="contained"
                className="buttonColorDelete"
                onClick={() => handleActionDelete(value)}
              >
                -
              </Button>
            </div>
          );
        },
      },
    },
  ];

  // all options
  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    print: false,
    download: false,
    viewColumns: false,
    responsive: "vertical",
    tableBodyHeight: "400px",
    tableBodyMaxHeight: "100%",
    rowsPerPage: [100],
  };

  // to be del options
  const option2 = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
    print: false,
    download: false,
    viewColumns: false,
    responsive: "vertical",
    tableBodyHeight: "400px",
    tableBodyMaxHeight: "100%",
    rowsPerPage: [100],
  };

  let productData = [];

  const getAllProducts = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });
    const [product] = await axios.all([pData]);

    if (product.data.success) {
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          dataIndex: index + 1,
          id: data.id,
          name: data.name,
          category: data.categories,
          sku: data.sku,
          barcode: data.barcode,
          cost: data.cost,
          quantity: data.quantity,
        };
        productData = [...productData, newdata];
        return productData;
      });
      setWarehouses([...warehouses, ...productData]);
    }
    setIsActive(false);
  };

  useEffect(() => {
    getAllProducts();
  }, [refresh, page, rowsPerPage]);

  return (
    // Loading Overlay
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div style={{ height: "85vh", width: "100%" }}>
        {/* Heading */}
        <div className="Texts_Display">Bulk Delete Variant List</div>

        {/* Tables */}
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-evenly",
            alignItems: "center",
            // height: "60vh"
          }}
        >
          {/* 1st table */}
          <div style={{ width: "100%" }}>
            <Grid style={{ width: "100%" }} item xs={12} sm={12}>
              <Card style={{ width: "100%" }}>
                <MuiThemeProvider>
                  <MUIDataTable
                    style={{ th: { backgroundColor: "grey" } }}
                    title={
                      <div>
                        <div
                          style={{
                            fontSize: "1.25rem",
                            fontFamily: "Arial",
                            fontWeight: "500",
                            lineHeight: "1.6",
                            letterSpacing: "0.0075em",
                          }}
                        >
                          All Available Variants
                        </div>
                        <br />
                        <div>
                          <Link onClick={addAllVariants}>
                            Add Loaded variants to the Variants to Delete List (
                            {warehouses.length}/{totalCount})
                          </Link>
                        </div>
                      </div>
                    }
                    data={warehouses}
                    columns={columns}
                    options={option}
                  />
                  <TablePagination
                    style={{ display: "none" }}
                    component="div"
                    count={totalCount}
                    page={page}
                    onPageChange={handleChangePage}
                    rowsPerPage={rowsPerPage}
                    onRowsPerPageChange={handleChangeRowsPerPage}
                  />
                </MuiThemeProvider>
              </Card>
            </Grid>
          </div>
          {/* 2nd table */}
          <div style={{ width: "100%" }}>
            <Grid style={{ width: "100%" }} item xs={12} sm={12}>
              <Card style={{ width: "100%" }}>
                <MuiThemeProvider>
                  <MUIDataTable
                    style={{ th: { backgroundColor: "grey" } }}
                    title={
                      <div>
                        <div
                          style={{
                            fontSize: "1.25rem",
                            fontFamily: "Arial",
                            fontWeight: "500",
                            lineHeight: "1.6",
                            letterSpacing: "0.0075em",
                          }}
                        >
                          Variants to Delete List
                        </div>
                        <br />
                        <div>
                          <Link onClick={removeAllVariants}>
                            Remove All Variants from the Variants to Delete List
                            ({selectedForDelete.length})
                          </Link>
                        </div>
                      </div>
                    }
                    data={selectedForDelete}
                    columns={columns2}
                    options={option2}
                  />
                  <TablePagination
                    style={{ display: "none" }}
                    component="div"
                    count={totalCount2}
                    page={page2}
                    onPageChange={handleChangePage2}
                    rowsPerPage={rowsPerPage2}
                    onRowsPerPageChange={handleChangeRowsPerPage2}
                  />
                </MuiThemeProvider>
              </Card>
            </Grid>
          </div>
        </div>

        {/* Buttons */}
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            alignItems: "center",
            // padding: "20px",
          }}
        >
          <div>
            <Button variant="contained" onClick={handleClose}>
              Cancel
            </Button>
          </div>
          <div>
            <Button
              onClick={deleteProduct}
              className="buttonColorAdd"
              variant="contained"
            >
              Delete
            </Button>
          </div>
        </div>
      </div>
    </LoadingOverlay>
  );
};

export default DeleteProducts;
