import React from "react";
import { makeStyles, styled } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import orderCSVImg from "../../Assets/Img/ordercsvImg.svg";
import csvTemplate from '../../Assets/sfapp_sample_order_csv_upload.csv'

const Input = styled("input")({
  display: "none",
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "97%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
    height: "90vh",
  },

  NesstedRoot: {
    width: "100%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
  },

  csvBody: {
    width: "100%",
    height: "90vh",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    border: '1px solid rgb(231, 237, 248)',
    // boxShadow: '1px 1px',
  },

  orderCSVButtons: {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-evenly",
    width: "30%",
    paddingTop: "10px",
  },

  uploadButton: {
    display: "block",
    padding: "8px",
    marginTop: "3px",
    borderRadius: "3px",
    fontSize: "14px",
    width: "190px",
    lineHeight: "20px",
    cursor: "pointer",
    textAlign: "center",
    fontWeight: "bold",
    color: "rgb(255, 255, 255)",
    backgroundColor: "rgb(50, 129, 253)",
    "&:hover": {
      background: "rgb(50, 129, 253)",
    },
  },
}));

export default function OrderCSV() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <header className="headerOrder">
        <div className="header__row">
          <div className="Texts_Display">CSV Management</div>
        </div>
        <body className={classes.csvBody}>
          <div>
            <div>
              <img src={orderCSVImg} />
              {/* <Image></Image> */}
            </div>
            <div>
              <Typography>You haven't uploaded any batch yet.</Typography>
            </div>
          </div>
          <div className={classes.orderCSVButtons}>
            <div>
              <a
                href={csvTemplate}
                download="csvTemplate"
                target="_blank"
              >
                <Button
                  style={{
                    border: "1px solid rgb(192, 204, 219)",
                    background:
                      "linear-gradient(rgb(255, 255, 255) 0%, rgb(249, 250, 252) 100%)",
                    width: "190px",
                  }}
                >
                  Download Template
                </Button>
              </a>
            </div>
            <div>
              {/* <Button className={classes.uploadButton}>Upload Orders</Button> */}
              <label htmlFor="contained-button-file">
                <Input
                  accept="doc/*"
                  id="contained-button-file"
                  multiple
                  type="file"
                />
                <Button
                  className={classes.uploadButton}
                  variant="contained"
                  component="span"
                >
                  Upload Orders
                </Button>
              </label>
            </div>
          </div>
        </body>
      </header>
    </div>
  );
}
