import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import {
  Box,
  Card,
  Container,
  Button,
  TextField,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogTitle,
  DialogContent,
  TablePagination,
  Modal,
} from "@material-ui/core";
import Dialog from "@material-ui/core/Dialog";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import * as Yup from "yup";
import { Form, FormikProvider, useFormik } from "formik";
import "react-toastify/dist/ReactToastify.css";
import { ToastContainer, toast } from "react-toastify";
import LoadingOverlay from "react-loading-overlay";
import QRCodeDialogBox from "./QRCodeDialogBox";
import routes from "../../routes/routes";

import { useDispatch, useSelector } from "react-redux";

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};


export default function BinsList() {
  const theme = useTheme();
  const [page, setPage] = useState(0);
  // this state holds the bin data created in the useEffect....
  const [bins, setBins] = useState([]);
  // o stands for orignal data for the bins.....
  const [obins, setOBins] = useState([]);
  const [racks, setRacks] = useState([]);
  const [shelfs, setShelfs] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [warehouses, setWarehouses] = useState([]);
  const [isQRModal, setIsQRModal] = useState(false);
  const [isSelectBox, setIsSelectBox] = useState("");
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [modalOpen, setModalOpen] = React.useState(false);
  
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)


  let binsData = [];

  const handleModalOpen = () => setModalOpen(true);

  const handleModalClose = () => setModalOpen(false);

  const EditClick = (event, _id, tableMeta) => {
    const data = bins.at(tableMeta.rowIndex);
    const rowdata = obins.find((item) => item._id === data._id);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleCloseModal = () => {
    setIsQRModal(false);
    setIsOpenModal(false);
    setIsSelectBox("");
    setRefresh(!refresh);
  };

  const handleModal = () => {
    setIsOpenModal(true);
  };

  const handleSelect = (e) => {
    const obj = { warehouse: "racks", rack: "shelfs" };
    const { name, value } = e.target;
    let newUrl = "";

    if (name === "warehouse") {
      newUrl= routes.api.racks.getRacksByWhId.url
    }
    if (name === "rack") {
      newUrl= routes.api.shelfs.getShelfsByRackId.url
    }
    if (name) {
      axios({
        method: "get",
        url: newUrl,
        params: { _id: value },
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            setIsSelectBox(obj[name]);
            if (name === "warehouse") {
              setRacks(response.data.racks);
            }
            if (name === "rack") {
              setShelfs(response.data.shelfs);
            }
          }
        }
      });
    }
  };

  const columns = [
    {
      name: "w_no",
      label: "Warehouse No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `W${value}`,
      },
    },
    {
      name: "r_no",
      label: "Rack No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `R${value}`,
      },
    },
    {
      name: "s_no",
      label: "Shelf No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `S${value}`,
      },
    },
    {
      name: "no",
      label: "Bin No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `B${value}`,
      },
    },

    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  const getAllBins = async () => {
    setIsActive(true)

    const wData = axios({
      method: "get",
      url: routes.api.warehouses.getAllWarehouses.url,
       headers: {Authorization: "Bearer " + userToken},
    });

    const bData = axios({
      method: "get",
      url: routes.api.bins.getAllBins.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const [warehouse, bin] = await axios.all([wData, bData]);

    if (warehouse.data.success) {
      setWarehouses(warehouse.data.warehouses);
    }

    if (bin.data.success) {
      setTotalCount(bin.data.bins.length);

      bin.data.bins.map((data) => {
        const newdata = {
          _id: data._id,
          no: data.no,
          s_no: data.shelf_id.no,
          r_no: data.shelf_id.rack_id.no,
          w_no: data.shelf_id.rack_id.warehouse_id.no,
        };
        binsData = [...binsData, newdata];
        return binsData;
      });
      setBins(binsData);
      setOBins(bin.data.bins);
    }
    setIsActive(false)
  };

  useEffect(() => {
    getAllBins();
  }, [refresh, page, rowsPerPage]);

  const notify = () => {
    console.log("toast");
    toast.success("Added Successfully!", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const binSchema = Yup.object().shape({
    shelf_id: Yup.string().required("Required"),
    noOfBins: Yup.number()
      .required("Required")
      .positive("Should be Positive")
      .integer(),
  });

  const formik = useFormik(
    {
    enableReinitialize: true,
    initialValues: {
      noOfBins: 1,
      shelf_id: "",
    },

    validationSchema: binSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      axios({
        method: "post",
        url: routes.api.bins.addNewBins.url,
        data: values,
        headers: {Authorization: "Bearer " + userToken},
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            notify()
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
      <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              onClick={handleModal}
              className="primaryButton"
            >
              New Bins
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={bins}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  // rowsPerPageOptions={[3, 6, 9]}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>

          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="shelf_id"
            editItem={editItem}
            onClose={handleCloseModal}
            />
            </Box>
          </Modal>
        ) : null}
        {isOpenModal ? (
          <Dialog open={isOpenModal} onClose={handleCloseModal}>
            <DialogTitle>Add Bins</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      {isSelectBox === "shelfs" ? (
                        <TextField
                          select
                          fullWidth
                          label="Shelfs"
                          {...getFieldProps("shelf_id")}
                          SelectProps={{ native: true }}
                          error={Boolean(touched.shelf_id && errors.shelf_id)}
                          helperText={touched.shelf_id && errors.shelf_id}
                        >
                          <option value="" />
                          {shelfs.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.rack_id.warehouse_id.no}/R${option.rack_id.no}/S${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {isSelectBox === "racks" ? (
                        <TextField
                          select
                          fullWidth
                          name="rack"
                          label="Racks"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {racks.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.warehouse_id.no}/R${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {isSelectBox === "" ? (
                        <TextField
                          select
                          fullWidth
                          name="warehouse"
                          label="Warehouse"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {warehouses.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.no}/${option.location}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      <TextField
                        // select
                        fullWidth
                        label="No of Bin"
                        inputProps={{ type: "number" }}
                        {...getFieldProps("noOfBins")}
                        error={Boolean(touched.noOfBins && errors.noOfBins)}
                        helperText={touched.noOfBins && errors.noOfBins}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : null}
      </Container>
    </div>
    </LoadingOverlay>
  );
}
