import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import CustomTextField from "../components/CustomTextField";
import CustomButton from "../components/CustomButton";
import { setAuthInfo } from "../Action/Auth";
import { createNotification } from "../components/Toast";
import api from "../apiCalls/api";
import { useDispatch, useSelector } from "react-redux";
import DarkMode from "../components/DarkMode";
import loginImage from "../Assets/Img/loginImage.png";
import appLogo from "../Assets/Img/appLogo.png";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import InputLabel from "@material-ui/core/InputLabel";
import routes from '../routes/routes'
import axios from "axios";  
import axiosInterceptor from './axiosInceptor'
import { Button } from "@material-ui/core";

// console.log("axiosIntercepto", axiosInterceptor)

const useStyles = makeStyles({
  root: {
    width: "100%",
    maxWidth: 500,
  },
});

export default function Login(props) {
  const dispatch = useDispatch();
  const classes = useStyles();
  const [password, setPassword] = useState("123456");
  const [email, setEmail] = useState("test1@gmail.com")
  const [error, setError] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [emailMessage, setEmailMessage] = useState("");
  const [passwordMessage, setPasswordMessage] = useState("");
  const [passwordError, setPasswordError] = useState(false);
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  const [windowDimenion, detectHW] = useState({
    winWidth: window.innerWidth,
    winHeight: window.innerHeight,
  })
  
  const detectSize = () => {
    detectHW({
      winWidth: window.innerWidth,
      winHeight: window.innerHeight,
    })
  }
const screenWidth = window.innerWidth;
  const MobileScreen = window.screen.width
  console.log("screenWidth", MobileScreen)
  // const userAuth = useSelector((state) => state.auth);
  useEffect(() => {
    if(userToken){
      history.push('/dashbord')
    }
  }, []);

  const authenticate =  (e) => {
    e.preventDefault();
    if (email === "" || email === null || email === undefined ){
      setEmailMessage("Enter Email")
      return false
    }
    if (password === "" || password === null || password === undefined ){
      setPasswordMessage("Enter Password")
      return false
    }
    if (email.length <= 3 || password.length <= 3 || error || loading) {
      setError(true);
    } else {
    setLoading(true);
    axios.post(routes.api.authentication.login.url , { email, password })
    .then((res) => {

        if(res.data.status === 401){
          console.log("res", res.data.message)
          createNotification("error", res.data.message);
        }
        if (res.data.status === 422) {
          createNotification("error", res.data.message, "Status");
          console.log("message", res.data);
        } else {
          // console.log("dispatch", dispatch())
          dispatch(setAuthInfo(res.data));
          console.log("response", res.data.token);
          localStorage.setItem("JWT", res.data.token);
          // localStorage.setItem("UserId", res.data.message.id);
          createNotification("success", "Login Successfully");
          axios.defaults.headers.common['Authorization'] =  `Bearer ${res.data.token}`;
          // console.log("axios:>>>>", axios.defaults)
              // localStorage.setItem("JWT", "token");
          history.push("/dashbord");
        }
        setLoading(false);
      })
      .catch((err) => {

        if(err){
          if(err.response && err.response.status === 401){
            createNotification("error", err.response.data.message);
          }
        }
        setLoading(false);
      });
    }
  };
  return (
    <div className="login-container">
    <div className="row">
    { MobileScreen === 400 || MobileScreen <= 410 ? 
    (<div className="col-md-6  col-lg-7 login-form-2">
      <h3>  <img src={appLogo} alt=""/></h3>
      <h2>  Welcome Back</h2> 
          <div className="form-group">
            <label className="labelform">Email</label>
              <input type="text"
              name="email"
              className="form-control input_field" 
              placeholder="Your Email *"
              value={email}
              error={emailError}
              message={emailMessage}
              onChange={(e) => {
                setEmail(e.target.value);
               email ? setEmailError(false) : setEmailError(true);
              }}
              />
              <span className="text-danger text-14">{emailMessage}</span>
          </div>
          <div className="form-group">
          <label>Password</label>
              <input type="password"
               name = "password"
              className="form-control input_field"
              message="Please Enter Value"
              value={password}
              error={passwordError}
              message={passwordMessage}
              onChange={(e)=>{setPassword(e.target.value);
              password ? setPasswordError(false) : setPasswordError(true)
              }}
              placeholder="Your Password *"  />
               <span className="text-danger text-14">{passwordMessage}</span>
          </div>
          <div className="form-group">
              <Link to ="/forgot-password" className="btnForgetPwd" value="Login">Forget Password?</Link>
          </div>
          <div className="form-group">
              <Button className="btnSubmit"onClick={authenticate} >
                {loading ? <div className="loader"></div>: 'login'}
              </Button>
          </div>
          <div className="deviderMain">
            <hr style={{width:'40%' , height:'1px'}}/>
          <span > OR </span>
          <hr style={{width:'40%' , height:'1px'}}/>
          </div>
          <div className="form-group">
              <input type="submit" className="btnSubmit" value="Login with another provider" />
          </div>
          <div className="signupLink">
            <p>Dont have an account ?</p>
            <Link to ="/register" className="RedirectionLink" value="Login">Register?</Link>
            </div>
      </div>
        ):
        (
          <>
      <div className="col-md-6 col-lg-5 login-form-1">
        <img src={loginImage} alt=""/>
      </div>
        <div className="col-md-6  col-lg-7 login-form-2">
        <h3>  <img src={appLogo} alt=""/></h3>
        <h2>  Welcome Back</h2> 
            <div className="form-group">
              <label className="labelform">Email</label>
                <input type="text"
                name="email"
                className="form-control input_field" 
                placeholder="Your Email *"
                value={email}
                error={emailError}
                message={emailMessage}
                onChange={(e) => {
                  setEmail(e.target.value);
                 email ? setEmailError(false) : setEmailError(true);
                }}
                />
                <span className="text-danger text-14">{emailMessage}</span>
            </div>
            <div className="form-group">
            <label>Password</label>

                <input type="password"
                 name = "password"
                className="form-control input_field"
                message="Please Enter Value"
                value={password}
                error={passwordError}
                message={passwordMessage}
                onChange={(e)=>{setPassword(e.target.value);
                password ? setPasswordError(false) : setPasswordError(true)
                }}
                placeholder="Your Password *"  />
                 <span className="text-danger text-14">{passwordMessage}</span>
            </div>
            <div className="form-group">
                <Link to ="/forgot-password" className="btnForgetPwd" value="Login">Forget Password?</Link>
            </div>
            <div className="form-group">
                <Button className="btnSubmit"onClick={authenticate} >
                  {loading ? <div className="loader"></div>: 'login'}
                </Button>
            </div>
            <div className="deviderMain">
              <hr style={{width:'40%' , height:'1px'}}/>
            <span > OR </span>
            <hr style={{width:'40%' , height:'1px'}}/>
            </div>
            <div className="form-group">
                <input type="submit" className="btnSubmit" value="Login with another provider" />
            </div>
            <div className="signupLink">
              <p>Dont have an account ?</p>
              <Link to ="/register" className="RedirectionLink" value="Login">Register?</Link>
              </div>
        </div>
        </>
        )
    }
        </div>
    </div>

  );
}
