import React, { useState, useEffect } from "react";
import MUIDataTable from "mui-datatables";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import { Form, FormikProvider, useFormik } from "formik";
import axios from "axios";
import Dialog from "@material-ui/core/Dialog";
import {
  Box,
  Card,
  Container,
  Button,
  TextField,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogTitle,
  DialogContent,
  TablePagination,
  Modal,
} from "@material-ui/core";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Yup from "yup";
import routes from "../../routes/routes";
import QRCodeDialogBox from "./QRCodeDialogBox";
import LoadingOverlay from "react-loading-overlay";
import { useDispatch, useSelector } from "react-redux";

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

export default function ShelfsList() {
  const [isActive, setIsActive] = useState(false);
  const [racks, setRacks] = useState([]);
  const [warehouses, setWarehouses] = useState([]);
  // this state holds the shelf data created in the useEffect....
  const [shelfs, setShelfs] = useState([]);
  // o stands for orignal data for the shelfs.....
  const [oshelfs, setOShelfs] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [isRack, setIsRack] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)

  
  const theme = useTheme();

  const handleModalOpen = () => setModalOpen(true);

  const handleModalClose = () => setModalOpen(false);

  const EditClick = (event, _id, tableMeta) => {
    const data = shelfs.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = oshelfs.find((item) => item._id === data._id);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleCloseModal = () => {
    setIsQRModal(false);
    setIsOpenModal(false);
    setIsRack(false);
    setRefresh(!refresh);
  };

  const handleModal = () => {
    setIsOpenModal(true);
  };

  const handleSelect = (e) => {
    setIsRack(true);
    const { name, value } = e.target;
    let newUrl = "";
    if (name === "warehouse") {
      newUrl= routes.api.racks.getRacksByWhId.url
    }
    if (name) {
      axios({
        method: "get",
        url: newUrl,
        params: { _id: value },
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            setRacks(response.data.racks);
          }
        }
      });
    }
  };

  const columns = [
    {
      name: "w_no",
      label: "Warehouse No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `W${value}`,
      },
    },
    {
      name: "r_no",
      label: "Rack No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `R${value}`,
      },
    },
    {
      name: "no",
      label: "Shelf No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `S${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let shelfsData = [];

  const getAllShelfs = async () => {
    setIsActive(true)

    const wData = axios({
      method: "get",
      url: routes.api.warehouses.getAllWarehouses.url,
      headers: {Authorization: "Bearer " + userToken},
    });

    const sData = axios({
      method: "get",
      url: routes.api.shelfs.getAllShelfs.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const [shelf, warehouse] = await axios.all([sData, wData]);

    if (shelf.data.success) {
      setTotalCount(shelf.data.totalDoc);

      shelf.data.shelfs.map((data) => {
        const newdata = {
          _id: data._id,
          no: data.no,
          r_no: data.rack_id.no,
          w_no: data.rack_id.warehouse_id.no,
        };
        shelfsData = [...shelfsData, newdata];
        return shelfsData;
      });
      setShelfs(shelfsData);
      setOShelfs(shelf.data.shelfs);
    }
    if (warehouse.data.success) {
      setWarehouses(warehouse.data.warehouses);
    }
    setIsActive(false)
  };

  useEffect(() => {
    getAllShelfs();
  }, [refresh, page, rowsPerPage]);

  const notify = () => {
    toast.success("Added Successfully!", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const rackSchema = Yup.object().shape({
    rack_id: Yup.string().required("Required"),
    noOfShelfs: Yup.number()
      .required("Required")
      .positive("Should be Positive")
      .integer(),
  });

  const formik = useFormik(
    {
    enableReinitialize: true,
    initialValues: {
      noOfShelfs: 1,
      rack_id: "",
    },

    validationSchema: rackSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      axios({
        method: "post",
        url: routes.api.shelfs.addNewShelfs.url,
        data: values,
        headers: {Authorization: "Bearer " + userToken},
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            notify();
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });
  
  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
      <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              onClick={handleModal}
              className="primaryButton"
            >
              New Shelfs
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={shelfs}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="rack_id"
            editItem={editItem}
            // onClose={handleCloseModal}
          />
          </Box>
          </Modal>
        ) : null}
        {isOpenModal ? (
          <Dialog open={isOpenModal} onClose={handleCloseModal}>
            <DialogTitle>Add Racks</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      {isRack ? (
                        <TextField
                          select
                          fullWidth
                          label="Racks"
                          {...getFieldProps("rack_id")}
                          SelectProps={{ native: true }}
                          error={Boolean(touched.rack_id && errors.rack_id)}
                          helperText={touched.rack_id && errors.rack_id}
                        >
                          <option value="" />
                          {racks.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.warehouse_id.no}/R${option.no}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                      {!isRack ? (
                        <TextField
                          select
                          fullWidth
                          name="warehouse"
                          label="Warehouse"
                          onChange={(e) => handleSelect(e)}
                          SelectProps={{ native: true }}
                        >
                          <option value="" />
                          {warehouses.map((option) => (
                            <option key={`${option._id}`} value={option._id}>
                              {`W${option.no}/${option.location}`}
                            </option>
                          ))}
                        </TextField>
                      ) : null}
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      <TextField
                        // select
                        fullWidth
                        label="No of Shelfs"
                        inputProps={{ type: "number" }}
                        {...getFieldProps("noOfShelfs")}
                        error={Boolean(touched.noOfShelfs && errors.noOfShelfs)}
                        helperText={touched.noOfShelfs && errors.noOfShelfs}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : null}
      </Container>
    </div>
    </LoadingOverlay>
  );
}
