import React from "react";
import "./App.css";
import Login from "./views/Login";
import ForgotPassword from "./views/ForgotPassword";
import Register from "./views/Register";
import Dashboard from "./views/Dashboard/Dashboard";
import { Switch, Route } from "react-router-dom";
import { ToastContainer } from "./components/Toast";
import Layout from "./views/Layout/Layout";
import OrderDetail from "./views/Orders/OrderDetail";
import OrderCSV from "./views/Orders/OrderCSV";
import CreateOrder from "./views/Orders/CreateOrder";
import ProductDetail from "./views/Products/ProductDetail";
import CreateProducts from "./views/Products/CreateProducts";
import VendorDetail from "./views/Vendor/VendorDetail";
import CreateVendors from "./views/Vendor/CreateVendors";
import CategoryDetail from "./views/Category/CategoryDetail";
import CreateCategory from "./views/Category/CreateCategory";
import Warehouse from "./views/warehouse/Warehouse";
import EditProduct from "./views/Products/EditProduct";
import UpdateVendor from "./views/Vendor/UpdateVendor"
import UpdateCategory from "./views/Category/UpdateCategory";
import EditVendors from "./views/Products/EditVendors";

export default function Router() {
  return (
    <div>
      <ToastContainer />
      <Layout>
        <Switch>
          <Route exact path="/" component={Login} />
          <Route exact path="/register" component={Register} />
          <Route exact path="/forgot-password" component={ForgotPassword} />
          <Route exact path="/dashbord" component={Dashboard} />
          <Route exact path="/dashbord/order" component={OrderDetail} />
          <Route exact path="/dashbord/orderCSV" component={OrderCSV} />
          <Route
            exact
            path="/dashbord/createNewOrder"
            component={CreateOrder}
          />
          <Route exact path="/dashbord/product" component={ProductDetail} />
          <Route
            exact
            path="/dashbord/product/createProduct"
            component={CreateProducts}
          />
          <Route
            exact
            path="/dashbord/product/editProduct"
            component={EditProduct}
          />
          <Route
            exact
            path="/dashbord/product/editVendors"
            component={EditVendors}
          />
         <Route
            exact
            path="/dashbord/vendor/updateVendor"
            component={UpdateVendor}
            />
         <Route
            exact
            path="/dashbord/category/updateCategory"
            component={UpdateCategory}
            />



          <Route exact path="/dashbord/vendors" component={VendorDetail} />
          <Route
            exact
            path="/dashbord/vendors/createvendor"
            component={CreateVendors}
          />
          <Route exact path="/dashbord/category" component={CategoryDetail} />
          <Route
            exact
            path="/dashbord/category/createCategory"
            component={CreateCategory}
          />
          <Route
            exact
            path="/dashbord/WarehouseBuilder"
            component={Warehouse}
          />
        </Switch>
      </Layout>
    </div>
  );
}
