import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import { useHistory } from "react-router-dom";
import roundReceipt from "@iconify/icons-ic/round-receipt";
import RacksList from "./RacksList";
import ShelfsList from "./ShelfsList";
import BinsList from "./BinsList";
import SubBinsList from "./SubBinsList";
import WarehouseList from "./WarehouseList";
import PartsCabinetList from "./PartsCabinetList";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "97%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
  },

  NesstedRoot: {
    width: "100%",
    paddingLeft: "3%",
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function ScrollableTabsButtonAuto(props) {
  const classes = useStyles();
  const history = useHistory();
  
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const CreateOrderHandleChange = () => {
    history.push(`/dashbord/product/createProduct`);
  };

  return (
    <div className={classes.root}>
      <header className="headerOrder">
        <div className="header__row">
          <div className="Texts_Display">Warehouse Builder</div>
          {/* <Button variant="contained" color="primary" className="buttonColor" onClick={CreateOrderHandleChange}>
                New Warehouse
             </Button> */}
        </div>
      </header>
      <AppBar position="static" color="white" className="AppBarStyle">
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
        >
          <Tab label="Warehouse" {...a11yProps(0)}  className="capitalizeText"/>
          <Tab label="Racks" {...a11yProps(1)} className="capitalizeText" />
          <Tab label="Shelfs" {...a11yProps(2)} className="capitalizeText" />
          <Tab label="Bin" {...a11yProps(3)} className="capitalizeText"/>
          <Tab label="Sub_Bins" {...a11yProps(4)} className="capitalizeText" />
          <Tab label="Part Cabinet" {...a11yProps(5)} className="capitalizeText"/>
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0} className="tabPanalOrchard">
        <WarehouseList />
      </TabPanel>
      <TabPanel value={value} index={1} className="tabPanalOrchard">
        <RacksList />
      </TabPanel>
      <TabPanel value={value} index={2} className="tabPanalOrchard">
        <ShelfsList />
      </TabPanel>
      <TabPanel value={value} index={3} className="tabPanalOrchard">
        <BinsList />
      </TabPanel>
      <TabPanel value={value} index={4} className="tabPanalOrchard">
        <SubBinsList />
      </TabPanel>
      <TabPanel value={value} index={5} className="tabPanalOrchard">
        <PartsCabinetList />
      </TabPanel>
    </div>
  );
}
