import { Button, DialogContent, DialogTitle, Grid } from "@material-ui/core";
import { divide } from "lodash";
import React, { useRef } from "react";
import QRCode from "react-qr-code";
import Barcode  from "react-barcode";
import ReactToPrint from "react-to-print";
import Typography from "@material-ui/core/Typography";
// import { DialogAnimate } from '../../components/animate';

const PRE_FIX = {
  warehouse: "W",
  warehouse_id: "R",
  rack_id: "S",
  shelf_id: "B",
  bin_id: "SB",
};

export default function QRCodeDialogBox({
  isQRModal,
  editItem,
  onClose,
  isType,
}) {
  const printRef = useRef();

  console.log("editItem", editItem)

  const handleClose = () => {
    onClose(false);
  };
  return (
    <div open={isQRModal} onClose={handleClose} style={{backgroundColor:'white'}} className="maindiv">
      {/* <div>{editItem.name}</div> */}
    
      <div>
        <Grid  style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            // style={{ textAlign: "center" }}
            ref={printRef}
          >
            <Grid item xs={12} sm={12}>
              <div className="barcodeNameDiv">
     
              <Typography
                
                    variant="h6"
                    color="black"
                  >
                    {editItem.productName}
                  </Typography>

                  <Typography
  
                    variant="h6"
                    color="black"
                  >
                    {editItem.quantity}
                  </Typography>

              </div>

              <div className="barcodeNameDivSecond">
                <div className="rentalDiv">
                <Typography variant="h7" color="black" className="typoGraphyfont">
                Rental Price
              </Typography>

              <div className="rentalPriceHorizantalBlock">
              <Typography  color="black" className="typoGraphyfont2">
                $
              </Typography>
              <Typography variant="h7" color="black" className="typoGraphyfont2">
              {editItem.cost}
              </Typography>
              {/* <Typography variant="h7" color="black" className="typoGraphyfont3">
             {editItem.quantity}
              </Typography> */}

              </div>

                </div>
                <div className="unitPricediv">
                <Typography variant="h7" color="black" className="typoGraphyfont">
                Unit Price 
              </Typography>
              <Typography variant="h7" color="black" className="typoGraphyfont2">
              {editItem.cost}
              </Typography>
                </div>
            


              </div>

              {/* "details":"${isType}${JSON.stringify(editItem[isType])}" */}
              <div className="d-flex justify-content-center">
              <Barcode value={editItem.barcode} />
              </div>
            
            </Grid>
            <Grid item xs={12} sm={12}>
              {isType === "warehouse" ? (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  {/* <strong style={{width:"100px",color:"gray"}}>Rack No: </strong> */}
                  {/* <p>{`W${editItem.no}`}</p> */}
                </Grid>
              ) : null}
              {isType === "warehouse_id" ? (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  {/* <strong style={{width:"100px",color:"gray"}}>Rack No: </strong> */}
                  <p>{`W${editItem[isType].no} / R${editItem.no}`}</p>
                </Grid>
              ) : null}
              {isType === "rack_id" ? (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  {/* <strong style={{width:"100px",color:"gray"}}>Rack No: </strong> */}
                  <p>{`W${editItem[isType].warehouse_id.no} / R${editItem[isType].no} / S${editItem.no}`}</p>
                </Grid>
              ) : null}
              {isType === "shelf_id" ? (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  {/* <strong style={{width:"100px",color:"gray"}}>Rack No: </strong> */}
                  <p>{`W${editItem[isType].rack_id.warehouse_id.no} / R${editItem[isType].rack_id.no} / S${editItem[isType].no} / B${editItem.no}`}</p>
                </Grid>
              ) : null}
              {isType === "bin_id" ? (
                <Grid
                  item
                  xs={12}
                  sm={12}
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    justifyContent: "space-around",
                  }}
                >
                  {/* <strong style={{width:"100px",color:"gray"}}>Rack No: </strong> */}
                  {console.log(editItem)}
                  <p>{`W${editItem[isType].shelf_id.rack_id.warehouse_id.no} / R${editItem[isType].shelf_id.rack_id.no} / S${editItem[isType].shelf_id.no} / B${editItem[isType].no} / SB${editItem.no}`}</p>
                </Grid>
              ) : null}
            </Grid>
          </Grid>
          <Grid item spacing={12} sm={12} style={{ textAlign: "center" }}>
            <ReactToPrint
              trigger={() => (
                <Button
                  // className={classes.button}
                  style={{ backgroundColor: "#185ADB" }}
                  type="submit"
                  variant="contained"
                  // onClick={() => setBool(true)}
                >
                  Print
                </Button>
              )}
              content={() => printRef.current}
            />
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
