import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import LoadingOverlay from "react-loading-overlay";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core/styles";
import {
  Grid,
  Card,
  MuiThemeProvider,
  TablePagination,
  Modal,
  Box,
} from "@material-ui/core";
import routes from "../../routes/routes";
import BarCodeDialogBox from "./BarCodeDialogBox";
import { useSelector } from "react-redux";




const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  // bgColor: 'background.paper',
  border: "2px solid #000",
  boxShadow: 24,
  // p: 4,
  backgroundColor: "white",
};

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
}));

console.log("process", process.env);

const ProductList = ({ isEdit, onClose, isAdd, onCloseAdd }) => {
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)


  const [open, setOpen] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [modalOpen, setModalOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(false);
  const [editItem, setEditItem] = useState({});
  const [products, setProducts] = useState([]);
  const [updateProduct, setUpdateProduct] = useState([]);
  const [addProductQuantity, setAddProductQuantity] = useState([]);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [isActive, setIsActive] = useState(false);

  const history = useHistory();
  const config = {
  headers: {
    Authorization: "Bearer " + userToken,
  },
};

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    // setPage(0);
  };

  const handleCloseModal = () => {
    // console.log('check');
    setIsQRModal(false);
    setIsOpenModal(false);
    setRefresh(!refresh);
  };

  const handleModalOpen = () => setModalOpen(true);

  const handleModalClose = () => setModalOpen(false);

  const EditClick = (event, _id, tableMeta) => {
    const data = products.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = products.find((item) => item._id === data._id);
    console.log(rowdata);
    setAnchorEl(event.currentTarget);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen();
  };

  const HandleEditQuantity = (data, e) => {
    const item = products[data];
    const newObject = { ...item };
    newObject["quantity"] = e.target.value;
    // products[data] = newObject+

    let array = updateProduct;

    array.push(newObject);
    console.log("array", array);
    // setProducts(products)
    setUpdateProduct(array);
  };

  const HandleAddQuantity = (data, e) => {
    // console.log("data", data, e.target.value)
    const item = products[data];
    console.log("item", item["quantity"]);
    const newObject = { ...item };
    newObject["quantity"] = e.target.value;
    console.log("newObject", newObject["quantity"]);

    const sum = +newObject["quantity"] + +item["quantity"];
    const addedObject = { ...item };

    addedObject["quantity"] = sum;
    let array = addProductQuantity;
    array.push(addedObject);

    setAddProductQuantity(array);
  };

  const handleClose = () => {
    onClose();
  };

  const handleClodeAdd = () => {
    onCloseAdd();
  };

  const handleUpdateProductCost = async (event) => {
    event.stopPropagation();
    await axios
      .put(routes.api.product.UpdateProductQuantity.url, {
        updateProduct,
      }, config)
      .then(function (response) {
        onClose();
        setRefresh(!refresh);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const handleAddProductQuantity = async () => {
    await axios
      .put(routes.api.product.AddProductQuantity.url, {
        addProductQuantity,
      }, config)
      .then(function (response) {
        onCloseAdd();
        setRefresh(!refresh);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  const handleProduct = (rowData) => {
    const data= rowData.parantCode

    history.push({
      pathname: '/dashbord/product/editProduct',
      state: {
        rowData,
        data: data,
      },
    })
  };

  const columns = [
    // _id
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "parantCode",
      label: "parantCode",
      options: {
        display: false,
      },
    },
    // dataIndex
    {
      name: "dataIndex",
      label: "#",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // productName
    {
      name: "name",
      label: "Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue, rowIndex) => (
          <span
            onClick={() => {
              handleProduct(products[tableMeta.rowIndex]);
            }}
            className="linkSpan"
          >
            {value}{" "}
          </span>
        ),
      },
    },
    // productCategory
    {
      name: "category",
      label: "Category",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // sku
    {
      name: "sku",
      label: "Sku",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // barcode
    {
      name: "barcode",
      label: "Barcode",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // quantity
    {
      name: "quantity",
      label: "Quantity",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) =>
          isEdit ? (
            <input
              type="text"
              defaultValue={value}
              onChange={(e) => HandleEditQuantity(tableMeta.rowIndex, e)}
              className="form-control"
            />
          ) : isAdd ? (
            <input
              type="text"
              placeholder={value}
              onChange={(e) => HandleAddQuantity(tableMeta.rowIndex, e)}
              className="form-control"
            />
          ) : (
            `${value}`
          ),
      },
    },
    // price
    {
      name: "price",
      label: "Price",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    // cost
    {
      name: "cost",
      label: "Cost",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{
              backgroundColor: "lightgray",
              textAlign: "start",
              textTransform: "capitalize",
            }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            Barcode
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let productData = [];

  const getAllProducts = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.product.getAllProducts.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const [product] = await axios.all([pData]);
    console.log(product, "product");
    if (product.data.success) {
      setTotalCount(product.data.totalDoc);

      product.data.doc.map((data, index) => {
        const newdata = {
          _id: data._id,
          dataIndex: index + 1,
          id: data.id,
          name: data.name,
          category: data.categories,
          sku: data.sku,
          barcode: data.barcode,
          price: data.price,
          cost: data.cost,
          quantity: data.quantity,
          parantCode:data.parantCode
        };
        productData = [...productData, newdata];
        return productData;
      });
      setProducts(productData);
      console.log("products", products);
      setIsActive(false);
      // setOWarehouses(products.data.products);
    }
  };

  useEffect(() => {
    getAllProducts();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div>
        <div className="subContainer">
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={products}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
            {isEdit ? (
              <div className="Footerdiv">
                <Button variant="contained" onClick={handleClose}>
                  Cancel
                </Button>
                <Typography>You are editing Inventory</Typography>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleUpdateProductCost}
                >
                  Save
                </Button>
              </div>
            ) : isAdd ? (
              <div className="Footerdiv">
                <Button variant="contained" onClick={handleClodeAdd}>
                  Cancel
                </Button>
                <Typography>You are editing Inventory</Typography>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={handleAddProductQuantity}
                >
                  Add Inventory
                </Button>
              </div>
            ) : (
              ``
            )}
          </Grid>
        </div>
      </div>
      {isQRModal ? (
        <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
            <BarCodeDialogBox
              isQRModal={isQRModal}
              isType="product"
              editItem={editItem}
              onClose={handleCloseModal}
            />
          </Box>
        </Modal>
      ) : null}
    </LoadingOverlay>
  );
};

export default ProductList;
