import React from "react";
import Header from "./Header";
import Sidebar from "./Sidebar";
import { withRouter } from "react-router-dom";
import Aux from "../../components/Aux_";
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useDispatch, useSelector } from "react-redux";






const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },

  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    // width:'205%'
  },
}));

const token = localStorage.getItem("JWT")
console.log("layoutToken", token)

function Layout(props) {
  const classes = useStyles();
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)
  return (
    <Aux>
 {userToken ?
 (     
 <div className="wrapper">
 <div id="sideBarContent">
   <Sidebar />
 </div>
 <main className={classes.content} id="content" className="mainContentLayout">
     <div className="row mainContentLayout "/>
     <div className = "col-lg-12 p-0 mainContentLayout ">
     {props.children}
     </div>
   </main>
</div>
):( 
     <main className={classes.content}>
     <div className={classes.toolbar} />
     {props.children}
   </main>
 )
 }
    </Aux>
  );
}
export default withRouter(Layout);
