import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import CommuteIcon from '@material-ui/icons/Commute';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import StoreMallDirectoryIcon from '@material-ui/icons/StoreMallDirectory';
import DirectionsBoatOutlinedIcon from '@material-ui/icons/DirectionsBoatOutlined';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import MailIcon from '@material-ui/icons/MoveToInbox';
import DashboardIcon from '@material-ui/icons/Dashboard';
import DashboardOutlinedIcon from '@material-ui/icons/DashboardOutlined';
import ReceiptOutlinedIcon from '@material-ui/icons/ReceiptOutlined';
import StoreMallDirectoryOutlinedIcon from '@material-ui/icons/StoreMallDirectoryOutlined';
import MonetizationOnOutlinedIcon from '@material-ui/icons/MonetizationOnOutlined';
import LocalAtmOutlinedIcon from '@material-ui/icons/LocalAtmOutlined';
import RestoreFromTrashOutlinedIcon from '@material-ui/icons/RestoreFromTrashOutlined';
import CategoryOutlinedIcon from '@material-ui/icons/CategoryOutlined';
import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import LocalOfferTwoToneIcon from '@material-ui/icons/LocalOfferTwoTone';
import CachedTwoToneIcon from '@material-ui/icons/CachedTwoTone';
import MoveToInboxOutlinedIcon from '@material-ui/icons/MoveToInboxOutlined';
import Sidebarlogo from '../../Assets/Img/Sidebarlogo.png'
import shipHero from '../../Assets/Img/shipHero.png'
import vendor from '../../Assets/Img/vendor.png'
import invoice1 from '../../Assets/Img/invoice1.png'
import store from '../../Assets/Img/store.png'
import ChatOutlinedIcon from '@material-ui/icons/ChatOutlined';
import orders from '../../Assets/Img/orders.png'
import shipment from '../../Assets/Img/shipment.png'
import postHero from '../../Assets/Img/postHero.png'
import { Link } from 'react-router-dom';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {setAuthInfo} from '../../Action/Auth'

import './sidebar-menu.css'
const drawerWidth = 240;
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    // backgroundColor:'blue'
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
    overflow:'hidden',
    backgroundColor:'blue'
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9) + 1,
    },
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  Nestedroot: {
    width: '100%',
    maxWidth: 360,
    marginTop:0,
    paddingTop:0,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  primaryColor:{
    fontSize:'12px',
    color:'#3f51b5',
    fontWeight:'600'
  }
}));

export default function MiniDrawer(props) {
  console.log("props", props)
  const classes = useStyles();
  const theme = useTheme();
  const dispatch = useDispatch();
  const [open, setOpen] = React.useState(false);
  const [openMenu, setOpenMenu] = React.useState(false);
  const [openReportMenu, setOpenReportMenu] = React.useState(false);
  const history = useHistory();

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleClick = () => {
    setOpenMenu(!openMenu);
  };
  const handleClickReport = () => {
    setOpenReportMenu(!openReportMenu);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handlelogout =()=>{
    dispatch(setAuthInfo({token:''}))
    localStorage.clear()
    history.push('/')

  }

  return (
    <div className={classes.root}>
      <CssBaseline />

      <Drawer
        variant="permanent"
        onMouseOver={handleDrawerOpen}
        onMouseLeave={handleDrawerClose}
        className={clsx(classes.drawer, {
          [classes.drawerOpen]: open,
          [classes.drawerClose]: !open,
        })}
        classes={{
          paper: clsx({
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          }),
        }}
      >
        {/* <div className={classes.toolbar}> */}
        <List className="imageLogopanding">
        <ListItem className="sidebarLogiImage">
              <ListItemIcon> <img src={Sidebarlogo} alt="" /> </ListItemIcon>
              <ListItemText className={classes.primaryColor}>
                <img src={shipHero} alt=""/> </ListItemText>
            </ListItem>
        </List>
          {/* <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton> */}
        {/* </div> */}
        <Divider />
        <List>
        <ListItem  component={Link} to="/dashbord">
              <ListItemIcon> <DashboardOutlinedIcon  size="small" style={{fontSize:'22px'}} /> </ListItemIcon>
              <ListItemText className={classes.primaryColor}> <span className="primaryColor">Dashboard</span></ListItemText>
            </ListItem>
        </List>
   <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      className={classes.Nestedroot}
    >

      <ListItem button onClick={handleClickReport}>
        <ListItemIcon>
        <LocalAtmOutlinedIcon  size="small" style={{fontSize:'22px'}} />
        
        {/* <img src={orders} alt="" style={{width:'50%' , color:'darkgray'}}/> */}

        </ListItemIcon>
        <ListItemText ><span className="defaultColor">Orders</span></ListItemText>
        {openMenu ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={openReportMenu} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button component={Link} to="/dashbord/order">
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon>
            <ListItemText > <span className="defaultColor">Orders</span></ListItemText>
          </ListItem>
          <ListItem button component={Link} to="/dashbord/orderCSV">
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon>
            <ListItemText ><span className="defaultColor">Upload CSV</span></ListItemText>
          </ListItem>
        </List>
      </Collapse>
    </List>
    <List>
        <ListItem component={Link} to="/dashbord/product">
              <ListItemIcon> <RestoreFromTrashOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}  /> </ListItemIcon>
              <ListItemText className={classes.defaultColor}> <span className="defaultColor">Products</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem component={Link} to="/dashbord/category">
              <ListItemIcon> <CategoryOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}  /> </ListItemIcon>
              <ListItemText className={classes.defaultColor}> <span className="defaultColor">Category</span></ListItemText>
            </ListItem>
        </List>

        <List>
        <ListItem component={Link} to="/dashbord/WarehouseBuilder">
              <ListItemIcon> <StoreMallDirectoryIcon style={{color:"#8392A7"}}  style={{fontSize:'22px'}} /> </ListItemIcon>
              <ListItemText className={classes.defaultColor}> <span className="defaultColor">WareHouse</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem >
              <ListItemIcon>
              <DirectionsBoatOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}  />
                 </ListItemIcon>
              <ListItemText className={classes.defaultColor} > <span className="defaultColor" >Shipping Plans</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem >
              <ListItemIcon> <CachedTwoToneIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}  /> </ListItemIcon>
              <ListItemText  className={classes.defaultColor}> <span className="defaultColor" >Returns</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem >
              <ListItemIcon>
              <MonetizationOnOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}/>
                   </ListItemIcon>
              <ListItemText className={classes.defaultColor}> <span className="defaultColor" >Post Hero</span></ListItemText>
            </ListItem>
        </List>
        <List
      component="nav"
      aria-labelledby="nested-list-subheader"
      className={classes.Nestedroot}
    >

      <ListItem button onClick={handleClick}>
        <ListItemIcon>
          <LocalAtmIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}/>
        </ListItemIcon>
        <ListItemText ><span className="defaultColor" >Reports</span></ListItemText>
        {openMenu ? <ExpandLess /> : <ExpandMore />}
      </ListItem>
      <Collapse in={openMenu} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          <ListItem button >
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon>
            <ListItemText  className={classes.primaryColor}> <span className="defaultColor" >Shipment</span></ListItemText>
          </ListItem>
          <ListItem button >
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon> 
            <ListItemText  className={classes.primaryColor}> <span className="defaultColor" >Shipped Item</span></ListItemText>
          </ListItem>
          <ListItem button >
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon>
            <ListItemText  className={classes.primaryColor}> <span className="defaultColor" >Order Permanently</span></ListItemText>
          </ListItem>
          <ListItem button >
            <ListItemIcon>
              {/* <StarBorder /> */}
            </ListItemIcon>
            <ListItemText  className={classes.primaryColor}> <span className="defaultColor" >Storage</span></ListItemText>
          </ListItem>
        </List>
      </Collapse>
    </List>
        <List>
        <ListItem component={Link} to="/dashbord/vendors">
              <ListItemIcon> <img src={vendor} alt="" style={{width:'45%' , color:'gray'}}/> </ListItemIcon>
              <ListItemText className={classes.primaryColor} > <span  className="defaultColor">Vendors</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem>
              <ListItemIcon>
              <ReceiptOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}/>
              </ListItemIcon>
              <ListItemText className={classes.primaryColor} > <span className="defaultColor">Invoices</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem >
              <ListItemIcon>
              <StoreMallDirectoryOutlinedIcon style={{color:"#8392A7"}} style={{fontSize:'22px'}}/>
                  </ListItemIcon>
              <ListItemText className={classes.primaryColor} > <span className="defaultColor">Store</span></ListItemText>
            </ListItem>
        </List>
        <List>
        <ListItem component={Link} to="#">
              <ListItemIcon> <ChatOutlinedIcon style={{color:"#8392A7"}}/> </ListItemIcon>
              <ListItemText className={classes.primaryColor} > <span className="defaultColor" >Help</span></ListItemText>
            </ListItem>
        </List>
        <List onClick={handlelogout}>
        <ListItem component={Link} to="#">
              <ListItemIcon > <ExitToAppIcon style={{color:"#8392A7"}}/> </ListItemIcon>
              <ListItemText className={classes.primaryColor} > <span className="defaultColor" >Logout</span></ListItemText>
            </ListItem>
        </List>

        <Divider />
      </Drawer>
 
    </div>
  );
}