import React, { useState, useEffect } from "react";
import axios from "axios";
import MUIDataTable from "mui-datatables";
import LoadingOverlay from "react-loading-overlay";
import { Grid, MuiThemeProvider, Card, TablePagination } from "@material-ui/core";
import routes from "../../routes/routes";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";



const CategoryList = (props) => {
  const [page, setPage] = useState(0);
  const history = useHistory();
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [isActive, setIsActive] = useState(false);
  const [category, setCategory] = useState([]);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };
  const handleProduct = (rowData) => {
    history.push("/dashbord/category/updateCategory", rowData);
  };


  const columns = [
    {
      name: "_id",
      label: "id",
      options: {
        display: false,
      },
    },
    {
      name: "dataIndex",
      label: "Number",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },

    {
      name: "categoryName",
      label: "Name",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue, rowIndex) => (
          <span
            onClick={() => {
              handleProduct(category[tableMeta.rowIndex]);
            }}
            className="linkSpan"
          >
            {value}{" "}
          </span>
        ),
  
      },
    },
    {
      name: "categoryDescription",
      label: " Category",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "categoryImage",
      label: " Image",
      options: {
        filter: true,
        sort: true,
        display:false,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "parentCategory",
      label: "Parent Category",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let productData = [];

  const getAllCategories = async () => {
    setIsActive(true);
    setRefresh(true);

    const pData = axios({
      method: "get",
      url: routes.api.category.getAllCategories.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const [product] = await axios.all([pData]);
    setTotalCount(product.data.totalDoc);

    product.data.doc.map((data, index) => {
      const newdata = {
        _id: data._id,
        dataIndex: index + 1,
        categoryName: data.name,
        categoryDescription: data.description,
        categoryImage: data.image,
        parentCategory: data.parentCategory,
      };
      productData = [...productData, newdata];
      return productData;
    });

    setCategory(productData);
    setIsActive(false);
  };

  useEffect(() => {
    getAllCategories();
  }, [refresh, page, rowsPerPage]);

  return (
    <LoadingOverlay active={isActive} spinner text="Loading ...">
      <div>
        <div>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={category}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </div>
      </div>
    </LoadingOverlay>
  );
};

export default CategoryList;
