import React , {useEffect}from "react";
import { makeStyles } from '@material-ui/core/styles';
import CustomTextField from '../../components/CustomTextField'
import CustomeTextArea from '../../components/CustomeTextArea'
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import camera from '../../Assets/Img/camera.jpg'
import BaseUrl from '../constant'
import axios from 'axios'
import ImageUpload from './ImageUpload'
import routes from "../../routes/routes";
import { Link } from 'react-router-dom'
import InputLabel from "@material-ui/core/InputLabel";
import Button from '@material-ui/core/Button';
import itemImage from '../../Assets/itemImage.png'
import { useDispatch, useSelector } from "react-redux";
import { createNotification } from "../../components/Toast"; 
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { Typography } from "@material-ui/core";
import { useHistory } from "react-router-dom";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '97%',
    paddingLeft: '4%',
    paddingTop: '1%',
    backgroundColor: "#F9FAFC",
  },
  
}));
const CreateCategory = (props) => {
  const classes = useStyles();
  const [categoryNameError, setCategoryNameError] = React.useState(false)
  const [categoryName, setCategoryName] = React.useState();
  const [categoryNameMessage, setCategoryNameMessage] = React.useState('');
  const [categoryDescription, setCategoryDescription] = React.useState();
  const [categoryDescriptionError, setCategoryDescriptionError] = React.useState(false);
  const [categoryDescriptionMessage, setCategoryDescriptionMessage] = React.useState('');

  const history = useHistory();
  const [parentCategory, setPerentCategory] = React.useState();
  const [parentCategoryError, setParentCategoryError] = React.useState(false);
  const [parentCategoryMessage, setParentCategoryMessage] = React.useState('');
  const [error, setError] = React.useState(false);
  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
  });
  const [imageState, imageSetState] = React.useState({
    file: null,
    base64: null,
    objectUrl: null
  });
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)


  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  async function readDataUrl(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      // async event handlers
      reader.onload = e => resolve(reader.result);
      reader.onerror = e => reject(reader.error);
      reader.readAsDataURL(file);
    });
  }
  let inputFileRef = null;

  const handleChangePhotoButton = e => {
    e.preventDefault();
    inputFileRef.click();
  };
  const handleChangePhotoFileInput = e => {
    const target = e.currentTarget;
    const file = target.files.item(0);

    const data = new FormData();
    data.append('file', file)

    // validate file as image
    if (!file.type.startsWith("image/")) {
      alert("File is not an image");
      return;
    }

    // store reference to the File object and a base64 representation of it
    readDataUrl(file).then(dataUrl => {
      imageSetState({
        ...imageState,
        file,
        base64: dataUrl,
        objectUrl: URL.createObjectURL(file)
      });
    });

    axios.post(routes.api.category.addCategoryImage.url,data,
    { 
      headers: {
        'Content-Type': 'multipart/form-data',
          Authorization: "Bearer " + userToken
      }
    })
    .then(function (response) {
      // console.log("response", response);
    })
    .catch(function (error) {
      console.log(error);
    });
  };
  const handleSubmitCategory =(e)=>{
    e.preventDefault()
    if (
      imageState.file === "" ||
      imageState.file === null ||
      imageState.file === undefined
    ) {
      createNotification("error", "Please Add Image ");
      return false;
    }
    if (categoryName === "" || categoryName === null || categoryName === undefined ){
      setCategoryNameError(true)
      setCategoryNameMessage("Please Enter Name")
      console.log("categoryNameMessage",categoryNameMessage )
      return false
    }
    if (categoryDescription === "" || categoryDescription === null || categoryDescription === undefined ){
      setCategoryDescriptionError(true)
      setCategoryDescriptionMessage("Please Add Description")
      return false
    }
    if (parentCategory === "" || parentCategory === null || parentCategory === undefined ){
      setParentCategoryError(true)
      setParentCategoryMessage("Please Enter Parent Category")
      return false
    }
    axios.post(BaseUrl +'createCategory', {
      categoryName,
      categoryDescription,
      parentCategory,
      categoryImage:imageState.file.name
    },
    {headers: {Authorization: "Bearer " + userToken}
  })
    .then(function (response) {
      console.log(response);
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  const handleSubmitCategoryandCreateAnther =(e)=>{
    e.preventDefault()
    if (
      imageState.file === "" ||
      imageState.file === null ||
      imageState.file === undefined
    ) {
      createNotification("error", "Please Add Image ");
      return false;
    }
    if (categoryName === "" || categoryName === null || categoryName === undefined ){
      setCategoryNameError(true)
      setCategoryNameMessage("Please Enter Name")
      console.log("categoryNameMessage",categoryNameMessage )
      return false
    }
    if (categoryDescription === "" || categoryDescription === null || categoryDescription === undefined ){
      setCategoryDescriptionError(true)
      setCategoryDescriptionMessage("Please Add Description")
      return false
    }
    if (parentCategory === "" || parentCategory === null || parentCategory === undefined ){
      setParentCategoryError(true)
      setParentCategoryMessage("Please Enter Parent Category")
      return false
    }
    axios.post(BaseUrl +'createCategory', {
      categoryName,
      categoryDescription,
      parentCategory,
      categoryImage:imageState.file.name
    },
    {headers: {Authorization: "Bearer " + userToken}
  })
    .then(function (response) {
      console.log(response);
      window.location.reload();
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  const { base64, objectUrl } = imageState;
  const defaultImage = "http://via.placeholder.com/300";
  console.log("base64", base64)

  useEffect(() => {
    window.addEventListener('scroll', isSticky);
    return () => {
        window.removeEventListener('scroll', isSticky);
    };
});
  const isSticky = (e) => {
    const header = document.querySelector('.header-section');
    const scrollTop = window.scrollY;
    console.log("scrollTop",scrollTop )
    scrollTop >= 50 ? header.classList.add('is-sticky') : header.classList.remove('is-sticky');
};

  return (
    <div >
    <header className="headerCreateOrder header-section ">
        <div className="header__row">
          <div className="Texts_Display mt-2">
          <Breadcrumbs aria-label="breadcrumb">
            <Link color="inherit" to="/dashbord/category">
              All Categories
            </Link>
            <Typography color="textPrimary">Create Category</Typography>
          </Breadcrumbs>
          </div>
          <div style={{width:'40%', textAlign:'center'}}> 
          <Button  
            onClick={()=>history.push("/dashbord/category")} 
             style={{backgroundColor:'lightgray', color:'black', marginRight:'1%'}}
             >Go Back</Button>
            <Button
            onClick={handleSubmitCategory}
            color="primary"  variant="contained" style={{backgroundColor:'#3498DB', color:'white'}}>
              Save and Create Another
              </Button>
          <Button
            onClick={handleSubmitCategoryandCreateAnther}
            color="primary"  variant="contained" style={{marginLeft:'1%', backgroundColor:'#3498DB', color:'white'}}>Save</Button>
          </div>
        </div>
      </header>
      {/* <Link to="/dashbord/vendors">Vendor</Link> */}
      <div className="row" style={{backgroundColor:'#F9FAFC', paddingLeft:'6%'}}>
        <div className="col-lg-8">
          <section className="leftColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Category details

                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                       Name
          </InputLabel>
                    <CustomTextField
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      placeholder="Give your vendor a name"
                      dummyClass="w-90"
                      error={categoryNameError}
                      message={categoryNameMessage}
                      onChange={(e) => {
                      setCategoryName(e.target.value);
                      setCategoryNameError(false) 
                    }}
                    />
                  </div>
                  <div className="pt-3 inputBlock">
                    <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                       Description
                 </InputLabel>
                    <CustomeTextArea
                      // value={dob}
                      autoComplete={false}
                      type="text"
                      className="w-90 Text-Area-Form"
                      error={categoryDescriptionError}
                      message={categoryDescriptionMessage}
                      onChange={(e) => {
                      setCategoryDescription(e.target.value);
                      setCategoryDescriptionError(false)

                    }}
                    />
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-4">
          <section className="rightColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Category Image
                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper h-50">
                <div className="flex-column w-full ">
                  <div className=" inputBlock">
                    <div className="grid-container">
                      <div className="grid-x grid-padding-x grid-padding-y">
                        <div className="cell">
                          <input
                            onChange={handleChangePhotoFileInput}
                            ref={input => (inputFileRef = input)}
                            style={{ display: "none" }}
                            type="file"
                          />
                          <button onClick={handleChangePhotoButton} className="button" style={{ width: '100%', height: '50vh' }}>
                            {!base64 ? (
                              <img
                                src={camera}
                                // ref={img => (this.imgRef = img)}
                                style={{ width: '100%', height: '50vh' }}
                              />
                            ) : (<img
                              src={base64}
                              // ref={img => (imgRef = img)}
                              style={{ width: '100%', height: '50vh' }}
                            />
                            )}
                          </button>
                        </div>
                      </div>
                      <div className="grid-x grid-padding-x grid-padding-y">
                        <div className="cell small-6">
                          <div className="callout">
                            <div className="card">
                              {/* <img
                  src={base64 || defaultImage}
                  ref={img => (this.imgRef = img)}
                  style={{width:'100%' , height:'50vh'}}
                /> */}
                            </div>
                          </div>
                        </div>

                        {/* <div className="cell small-6">
            <div className="callout">
              <h3>Object URL</h3>
              <div className="card">
                <img src={objectUrl || defaultImage} />
              </div>
            </div>
          </div> */}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
      </div>
      <div className="row" style={{backgroundColor:'#F9FAFC', paddingLeft:'6%'}}>
        <div className="col-lg-8">
          <section className="leftColumn">
            <section className="eMmHox">
              <div className="fxrGze">
                <div className="card-title">
                  <div className="hPSGav">
                    Category Perant Detail
                </div>
                </div>
                <div className="dEgCwM">
                  {/* <Button>Add Item</Button> */}
                </div>
              </div>
              <div className="order-detail-wrapper">
                <div className="flex-column w-full">
                  <div className="form_input_block" >
                    <div className="pt-3 twoinputBlock">
                      <InputLabel htmlFor="my-input" className="text-black text-14 pt-2">
                        Parent Category
                      </InputLabel>
                      <CustomTextField
                        // value={dob}
                        autoComplete={false}
                        type="text"
                        placeholder="Optional"
                        dummyClass="w-90"
                        error={parentCategoryError}
                        message={parentCategoryMessage}
                        onChange={(e) => {
                        setPerentCategory(e.target.value);
                        setParentCategoryError(false)

                      }}
                      />
                    </div>
                  </div>
                </div>
              </div>
            </section>
          </section>
        </div>
        <div className="col-lg-4 h-30">

        </div>
      </div>      
        <div style={{display:'flex', flexDirection:'row', justifyContent:'center', alignItems:'center',padding:'2%'}}>
          <Button variant="contained" color="primary" onClick={handleSubmitCategory}>Save Category</Button>
        </div>
    </div>
  );
}
export default CreateCategory;