import React from "react";
import MUIDataTable from "mui-datatables";
import { Icon } from "@iconify/react";
import plusFill from "@iconify/icons-eva/plus-fill";
import { Form, FormikProvider, useFormik } from "formik";
import { useState, useEffect } from "react";
import axios from "axios";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import {
  Box,
  Card,
  Container,
  Button,
  TextField,
  MuiThemeProvider,
  useTheme,
  Grid,
  DialogContent,
  TablePagination,
  Modal,
} from "@material-ui/core";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Yup from "yup";
import routes from "../../routes/routes";
import QRCodeDialogBox from "./QRCodeDialogBox";
import LoadingOverlay from "react-loading-overlay";
import { useDispatch, useSelector } from "react-redux";

// ----------------------------------------------------------------------

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};


export default function WarehouseList() {
  const [warehouses, setWarehouses] = useState([]);
  const [owarehouses, setOWarehouses] = useState([]);
  const [editItem, setEditItem] = useState({});
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [isQRModal, setIsQRModal] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [totalCount, setTotalCount] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = React.useState(false);
  const [isActive, setIsActive] = useState(false);
  const { userToken, isAuthenticated } = useSelector((state) => state.Auth);
  console.log("userToken", userToken)

  const theme = useTheme();

  const handleModalOpen = () => setModalOpen(true);

  const handleModalClose = () => setModalOpen(false);

  const EditClick = (event, _id, tableMeta) => {
    const data = warehouses.at(tableMeta.rowIndex);
    // getting the orignal item.....
    const rowdata = owarehouses.find((item) => item._id === data._id);
    setEditItem(rowdata);
    setIsQRModal(true);
    handleModalOpen()
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleClose = () => {
    setIsOpenModal(true);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleCloseModal = () => {
    setIsQRModal(false);
    setIsOpenModal(false);
    setRefresh(!refresh);
  };

  const handleModal = () => {
    setIsOpenModal(true);
  };

  const notify = () => {
    toast.success("Added Successfully!", {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: true,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const columns = [
    {
      name: "no",
      label: "Warehouse No",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `W${value}`,
      },
    },
    {
      name: "location",
      label: "Address",
      options: {
        filter: true,
        sort: true,
        customBodyRender: (value, tableMeta, updateValue) => `${value}`,
      },
    },
    {
      name: "_id",
      label: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => (
          <Button
            style={{ backgroundColor: "lightgray", textAlign: "start" }}
            onClick={(e) => EditClick(e, value, tableMeta)}
          >
            QR Code
          </Button>
        ),
      },
    },
  ];

  const option = {
    filterType: "dropdown",
    selectableRowsHideCheckboxes: true,
    pagination: false,
  };

  let warehousesData = [];

  const getAllWarehouse = async () => {
    setIsActive(true)

    const wData = axios({
      method: "get",
      url: routes.api.warehouses.getAllWarehouses.url,
      params: { limit: rowsPerPage, page: page },
      headers: {Authorization: "Bearer " + userToken},
    });

    const [warehouses] = await axios.all([wData]);

    if (warehouses.data.success) {
      setTotalCount(warehouses.data.warehouses.length);

      warehouses.data.warehouses.map((data) => {
        const newdata = { _id: data._id, no: data.no, location: data.location };
        warehousesData = [...warehousesData, newdata];
        return warehousesData;
      });
      setWarehouses(warehousesData);
      setOWarehouses(warehouses.data.warehouses);
    }
    setIsActive(false)
  };

  useEffect(() => {
    getAllWarehouse();
  }, [refresh, page, rowsPerPage]);

  const warehouseSchema = Yup.object().shape({
    location: Yup.string("String").required("Required"),
  });

  const formik = useFormik(
    {
    enableReinitialize: true,
    initialValues: {
      location: "",
    },

    validationSchema: warehouseSchema,

    onSubmit: (values, { setErrors, setSubmitting, resetForm }) => {
      axios({
        method: "post",
        url: routes.api.warehouses.addNewWarehouse.url,
        data: values,
        headers: {Authorization: "Bearer " + userToken},
      }).then((response) => {
        if (response) {
          if (response.data.success) {
            // enqueueSnackbar("Added Successfully", { variant: "success" });
            notify();
            setSubmitting(false);
            resetForm();
            handleCloseModal();
          }
        }
      });
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  return (
    <LoadingOverlay
      active={isActive}
      spinner
      text='Loading ...'
    >
    <div>
      <Container maxWidth="xl" style={{ padding: "0px" }}>
        <ToastContainer />
        <Grid style={{ display: "flex", flexDirection: "column" }}>
          <Grid
            item
            xs={12}
            sm={12}
            style={{ marginBottom: "5px", textAlign: "right" }}
          >
            <Button
              variant="contained"
              startIcon={<Icon icon={plusFill} width={20} height={20} />}
              className="primaryButton"
              onClick={handleModal}
            >
              New Warehouse
            </Button>
          </Grid>
          <Grid item xs={12} sm={12}>
            <Card>
              <MuiThemeProvider theme={theme}>
                <MUIDataTable
                  style={{ th: { backgroundColor: "grey" } }}
                  //   title="Employees List"
                  data={warehouses}
                  columns={columns}
                  options={option}
                />
                <TablePagination
                  component="div"
                  count={totalCount}
                  page={page}
                  onPageChange={handleChangePage}
                  rowsPerPage={rowsPerPage}
                  onRowsPerPageChange={handleChangeRowsPerPage}
                />
              </MuiThemeProvider>
            </Card>
          </Grid>
        </Grid>
        {isQRModal ? (
          <Modal
          open={modalOpen}
          onClose={handleModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>
          <QRCodeDialogBox
            isQRModal={isQRModal}
            isType="warehouse"
            editItem={editItem}
            onClose={handleCloseModal}
          />
          </Box>
          </Modal>
        ) : null}

        {isOpenModal ? (
          <Dialog
            onClose={handleClose}
            aria-labelledby="simple-dialog-title"
            open={isOpenModal}
          >
            <DialogTitle>Add Warehouse</DialogTitle>
            <DialogContent>
              <FormikProvider value={formik}>
                <Form autoComplete="off">
                  <Grid container spacing={2}>
                    <Grid item xs={12} sm={12}>
                      <TextField
                        // select
                        fullWidth
                        label="Warehouse Location"
                        // inputProps={{ type: 'number' }}
                        {...getFieldProps("location")}
                        error={Boolean(touched.location && errors.location)}
                        helperText={touched.location && errors.location}
                      />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                      <Box
                        sx={{
                          mt: 1,
                          display: "flex",
                          justifyContent: "center",
                        }}
                      >
                        <Button
                          //   onClick={handleCloseModal}
                          variant="contained"
                          color="secondary"
                          sx={{ mr: 1 }}
                          onClick={() => setIsOpenModal(false)}
                        >
                          Cancel
                        </Button>
                        <Button
                          type="submit"
                          variant="contained"
                          pending={isSubmitting}
                          onClick={(e) => {
                            e.preventDefault();
                            handleSubmit();
                          }}
                        >
                          Save
                        </Button>
                      </Box>
                    </Grid>
                  </Grid>
                </Form>
              </FormikProvider>
            </DialogContent>
          </Dialog>
        ) : (
          ""
        )}
      </Container>
    </div>
    </LoadingOverlay>
  );
}
